/* Link styles */
.logoLink {
  width: 50px;
  display: inline-block;
}

/* Activelink styles */
.activeLink {
  border: 4px solid #fbe0dc;
}
.activeLink > .linkText {
  background: #ff868e;
  color: #ffffff;
}

/* Logo img styles */
.logo {
  transition: all 0.5s;
}
.logo:hover {
  transform: scale(1.07);
}

/* Main text styles */
.heroText {
  margin-top: 178px;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 44px;
  line-height: 1.32;
}
.subText {
  margin-bottom: 80px;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.45;
  color: var(--second-black-color);
}

/* Main links section styles */
.linkWrapper {
  display: flex;
}

/* Inner link image styles  */
.votingImg {
  width: 100%;
  height: 100%;
}

/* Innet link text styles */
.linkText {
  display: block;
  width: 100%;
  height: 36px;
  margin: 0;
  margin-top: 10px;
  background: #ffffff;
  border-radius: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: center;
  text-decoration: none;
  font-family: 'Jost';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.34;
  letter-spacing: 2px;
  color: var(--main-pink-color);
}

/* Voting link styles */
.votingBcg {
  background: #b4b7ff;
  border: 4px solid rgba(255, 255, 255, 0.6);
  border-radius: 20px;
  width: 153px;
  height: 198px;
  margin-right: 18px;
  text-decoration: none;
}
.votingBcg:hover {
  border: 4px solid #fff;
}
.votingBcg:hover > .linkText {
  background: #ff868e;
  color: #fff;
}

